import React, { useReducer } from 'react';
import SongCard from './SongCard';
import storeRequestSender from '../store/requests';

const filterListReducer = (state, action) => {
    switch (action.type) {
        case 'FILTER_BY_NAME':
            return { ...state, name: action.payload };
        case 'CLEAR':
            return { ...state, name: '' };
        default:
            return state;
    }
};

const sortPlaylistsReducer = (state, action) => {
    const computeTotalListens = (playlist) => {
        if (!playlist || !playlist.songs) return 0;
        return (playlist.songs || []).reduce((acc, s) => acc + (s.listens || 0), 0);
    };

    const sortData = (data, sortBy) => {
        if (!data || data.length === 0) return data;
        const sorted = [...data];
        switch (sortBy) {
            case 'SORT_BY_uniqueListenersHiLo':
                return sorted.sort((a, b) => computeTotalListens(b) - computeTotalListens(a));
            case 'SORT_BY_uniqueListenersLoHi':
                return sorted.sort((a, b) => computeTotalListens(a) - computeTotalListens(b));
            case 'SORT_BY_playlistNameAZ':
                return sorted.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
            case 'SORT_BY_playlistNameZA':
                return sorted.sort((a, b) => (b.name || '').localeCompare(a.name || ''));
            case 'SORT_BY_ownerNameAZ':
                return sorted.sort((a, b) => (a.ownerEmail || '').localeCompare(b.ownerEmail || ''));
            case 'SORT_BY_ownerNameZA':
                return sorted.sort((a, b) => (b.ownerEmail || '').localeCompare(a.ownerEmail || ''));
            default:
                return sorted;
        }
    };

    switch (action.type) {
        case 'SET_PLAYLISTS':
            return { ...state, data: sortData(action.payload, state.sortBy) };
        case 'SORT_BY_uniqueListenersHiLo':
        case 'SORT_BY_uniqueListenersLoHi':
        case 'SORT_BY_playlistNameAZ':
        case 'SORT_BY_playlistNameZA':
        case 'SORT_BY_ownerNameAZ':
        case 'SORT_BY_ownerNameZA':
            return { ...state, sortBy: action.type, data: sortData(state.data, action.type) };
        default:
            return state;
    }
}

function PlaylistCatalogScreen() {
    const [playlists, dispatchPlaylists] = useReducer(sortPlaylistsReducer, { data: null, sortBy: 'SORT_BY_playlistNameAZ' });
    const [filterList, dispatchFilterList] = useReducer(filterListReducer, { name: '' });
    const [selectedPlaylist, setSelectedPlaylist] = React.useState(null);

    const handlePlaylistClick = (playlist) => {
        setSelectedPlaylist(playlist);
    }

    const handleSearch = async () => {
        try {
            const response = await storeRequestSender.getPlaylists();
            const data = await response.json();
            if (data.success) {
                let filtered = data.data || [];
                if (filterList.name && filterList.name.trim() !== '') {
                    const lower = filterList.name.toLowerCase();
                    filtered = filtered.filter(p => (p.name || '').toLowerCase().includes(lower));
                }
                dispatchPlaylists({ type: 'SET_PLAYLISTS', payload: filtered });
            } else {
                console.error('Failed to get playlists', data);
            }
        } catch (err) {
            console.error('Error fetching playlists: ', err);
        }
    }

    const computeTotalListens = (playlist) => (playlist?.songs || []).reduce((acc, s) => acc + (s.listens || 0), 0);

    let displayPlaylists = (playlists.data && playlists.data.length !== 0) ? playlists.data.map(playlist => (
        <div onClick={() => handlePlaylistClick(playlist)} key={playlist._id}>
            <div>{playlist.name}</div>
            <div>{playlist.songs?.length || 0} songs  {playlist.ownerEmail}</div>
            <div>Total Listens: {computeTotalListens(playlist)}</div>
        </div>
    )) : <div>No playlists found.</div>;

    return (
        <div>
            <div>
                <h1>Playlist Catalog</h1>
                <div>
                    <input
                        type="text"
                        placeholder="by name"
                        value={filterList.name}
                        onChange={(e) => dispatchFilterList({ type: 'FILTER_BY_NAME', payload: e.target.value })}
                        
                    />
                </div>
                <div>
                    { selectedPlaylist && (
                        <div>
                            <div>{selectedPlaylist.name}</div>
                            <div>{selectedPlaylist.ownerEmail}</div>
                            <div>
                                <div>Songs</div>
                                <div>
                                    { selectedPlaylist.songs?.length > 0 ? selectedPlaylist.songs.map((song, index) => (
                                        <SongCard song={song} index={index} isSongCatalog={false} key={index} />
                                    )) : <div>No songs in playlist</div> }
                                </div>
                            </div>
                        </div>
                    ) }
                </div>
                <div>
                    <button onClick={handleSearch}>Search</button>
                    <button onClick={() => dispatchFilterList({ type: 'CLEAR' })}>Clear</button>
                </div>
            </div>
            <div>
                { playlists.data === null ? <div>Click search to load your playlists.</div> : (
                    <div>
                        <div>
                            <div>
                                <span>Sort by:</span>
                                <select id="sortBy" value={playlists.sortBy} onChange={(e) => dispatchPlaylists({type: e.target.value})}>
                                    <option value="SORT_BY_uniqueListenersHiLo">Unique Listeners Hi Lo</option>
                                    <option value="SORT_BY_uniqueListenersLoHi">Unique Listeners Lo Hi</option>
                                    <option value="SORT_BY_playlistNameAZ">Playlist Name (A-Z)</option>
                                    <option value="SORT_BY_playlistNameZA">Playlist Name (Z-A)</option>
                                    <option value="SORT_BY_ownerNameAZ">Owner (A-Z)</option>
                                    <option value="SORT_BY_ownerNameZA">Owner (Z-A)</option>
                                </select>
                            </div>
                            <div>
                                <span>{playlists.data.length} Playlists</span>
                            </div>
                        </div>
                        <div>
                            { displayPlaylists }
                        </div>
                    </div>
                ) }
            </div>
        </div>
    );
}

export default PlaylistCatalogScreen;
